import { Loading as Spin } from '@/components/loading'
import styles from './add.less'
import _ from '@/i18n'
import { useState } from 'react'
import Slider from '@/components/slider'
import Btn from '@/components/btn'
import Info from './info'
import { amountRaw, calUsdPrice } from '@/common/utils'
import { InputNumber } from 'antd'
import Token from '@/components/chooseToken/token'
import { useDispatch } from 'umi'
import { Result } from '@/components/result/simple'
import { message } from '@/components/message'
import type { FutureStateType } from '@/models/dataType'
import { connect } from 'umi'

function AddMargin(props) {
  const dispatch = useDispatch()
  const { currentToken, loading, closeModal } = props
  const {
    userMargin: margin,
    collateralTokenObj: collateralToken,
    indexTokenObj: indexToken,
    isLong,
    pair,
  } = currentToken
  const { userTokenBalance } = collateralToken
  const [submiting, setSubmiting] = useState(false)
  const [amountIn, setAmountIn] = useState(0)

  const changeAmount = (value: number) => {
    setAmountIn(value)
  }

  const handleSubmit = async () => {
    setSubmiting(true)
    try {
      const res = await dispatch({
        type: 'future/increaseMargin',
        payload: {
          tokenId: collateralToken.id,
          params: {
            _collateralToken: collateralToken.id,
            _indexToken: indexToken.id,
            _isLong: isLong,
            _amountIn: amountRaw(amountIn.toString(), collateralToken.tokenDecimal),
          },
        },
      })

      if (res.hash) {
        Result({
          msg: _('success'),
          txid: res.hash,
          token: currentToken,
          onOk: () => {
            closeModal()
            dispatch({
              type: 'future/getPairs',
            })
          },
        })
        dispatch({
          type: 'future/getPositionList',
        })
      } else {
        message('Failed')
      }
    } catch (error: any) {
      message(error)
    }
    setSubmiting(false)
  }

  return (
    <Spin spinning={loading || submiting}>
      <div className={styles.container}>
        <div className={styles.card}>
          <div className={styles.label}>
            <div>
              {_('current_margin')}: ${calUsdPrice(margin, pair.userCollateralPrice)}
            </div>
            <div>
              {_('wallet_bal')}:&nbsp;
              <span className="primary" onClick={() => changeAmount(parseFloat(userTokenBalance))}>
                {userTokenBalance}
              </span>
            </div>
          </div>
          <div className={styles.value}>
            <InputNumber className="inputTxt" value={amountIn} onChange={changeAmount} min={0} />
            <Token data={collateralToken} />
          </div>
          <Slider amount={amountIn} onChange={changeAmount} max={userTokenBalance} />
        </div>
        <Info currentToken={currentToken} amountIn={amountIn} isAdd={true} />
        <Btn
          height={50}
          align="center"
          onClick={handleSubmit}
          conditions={[
            {
              handle: amountIn <= 0,
              txt: _('input_amount'),
            },
            {
              handle: amountIn > parseFloat(userTokenBalance || '0'),
              txt: _('lack_balance'),
            },
          ]}
        >
          {_('incrase_margin')}
        </Btn>
      </div>
    </Spin>
  )
}

const mapStateToProps = ({ future }: { future: FutureStateType }) => {
  return {
    ...future,
  }
}

export default connect(mapStateToProps)(AddMargin)
