import { Loading as Spin } from '@/components/loading'
import styles from './add.less'
import _ from '@/i18n'
import { useEffect, useState } from 'react'
import Slider from '@/components/slider'
import Btn from '@/components/btn'
import Info from './info'
import { amountRaw, formatSat } from '@/common/utils'
import { InputNumber } from 'antd'
import Token from '@/components/chooseToken/token'
import { useDispatch, connect } from 'umi'
import { Result } from '@/components/result/simple'
import { message } from '@/components/message'
import type { FutureStateType } from '@/models/dataType'
import { calcMaxDecreaseMargin } from '@1kx/typechain/helper/future'

function RemoveMargin(props) {
  const dispatch = useDispatch()
  const { currentToken, loading, closeModal } = props
  const {
    userMargin: margin,
    collateralTokenObj: collateralToken,
    indexTokenObj: indexToken,
    isLong,
    pair,
  } = currentToken
  const [submiting, setSubmiting] = useState(false)
  const [amountIn, setAmountIn] = useState(0)
  const [maxRemovable, setMaxRemovable] = useState('0')

  const changeAmount = (value: number) => {
    setAmountIn(value)
  }

  useEffect(() => {
    const max = calcMaxDecreaseMargin(currentToken, pair, pair.collateralPrice, pair.indexPrice)
    const maxStr = formatSat(max.toString(), pair.userCollateralTokenDecimal)
    // console.log('params:', currentToken, pair, pair.collateralPrice, pair.indexPrice)
    // console.log(max, maxStr)
    setMaxRemovable(maxStr)
  }, [])

  const handleSubmit = async () => {
    setSubmiting(true)
    try {
      const res = await dispatch({
        type: 'future/decreaseMargin',
        payload: {
          tokenId: collateralToken.id,
          params: {
            _collateralToken: collateralToken.id,
            _indexToken: indexToken.id,
            _isLong: isLong,
            _amountOut: amountRaw(amountIn.toString(), collateralToken.tokenDecimal),
          },
        },
      })
      if (res.hash) {
        Result({
          msg: _('success'),
          txid: res.hash,
          token: currentToken,
          onOk: () => {
            closeModal()
            dispatch({
              type: 'future/getPairs',
            })
          },
        })
        dispatch({
          type: 'future/getPositionList',
        })
      } else {
        message('Failed')
      }
    } catch (error: any) {
      message(error)
    }
    setSubmiting(false)
  }

  return (
    <Spin spinning={loading || submiting}>
      <div className={styles.container}>
        <div className={styles.card}>
          <div className={styles.label}>
            <div>
              {_('max_removable')}:{' '}
              <span className="primary" onClick={() => changeAmount(parseFloat(maxRemovable))}>
                {maxRemovable}
              </span>
            </div>
            <div>
              {_('current_margin')}: {margin}
            </div>
          </div>
          <div className={styles.value}>
            <InputNumber className="inputTxt" value={amountIn} onChange={changeAmount} min={0} />
            <Token data={collateralToken} />
          </div>
          <Slider amount={amountIn} onChange={changeAmount} max={maxRemovable} />
        </div>
        <Info currentToken={currentToken} amountIn={amountIn} isAdd={false} />
        <Btn
          height={50}
          align="center"
          onClick={handleSubmit}
          conditions={[
            {
              handle: amountIn <= 0,
              txt: _('input_amount'),
            },
            {
              handle: amountIn >= parseFloat(margin || '0'),
              txt: _('lack_balance'),
            },
          ]}
        >
          {_('withdraw_margin')}
        </Btn>
      </div>
    </Spin>
  )
}

const mapStateToProps = ({ future }: { future: FutureStateType }) => {
  return {
    ...future,
  }
}

export default connect(mapStateToProps)(RemoveMargin)
