import { Modal } from 'antd'
// import styles from './index.less'
import PanelMenu from '@/components/menu'
import { MarginMenuDatas } from '@/common/config'
import AddMargin from './add'
import RemoveMargin from './remove'
import { useState } from 'react'

export default function Liquidity(props) {
  const { visible, setVisible, currentToken } = props
  const [menutIndex, setMenuIndex] = useState(0)

  return (
    <Modal
      width={420}
      footer={null}
      open={visible}
      getContainer={() => document.getElementById('J_Page') || document.body}
      onCancel={() => setVisible(false)}
      closable={false}
      // wrapClassName={styles.modal}
    >
      <PanelMenu menuDatas={MarginMenuDatas} menuIndex={menutIndex} setMenuIndex={setMenuIndex} />
      <div style={{ display: menutIndex === 0 ? 'block' : 'none' }}>
        <AddMargin currentToken={currentToken} closeModal={() => setVisible(false)} />
      </div>
      <div style={{ display: menutIndex === 1 ? 'block' : 'none' }}>
        <RemoveMargin currentToken={currentToken} closeModal={() => setVisible(false)} />
      </div>
    </Modal>
  )
}
