import { calUsdPrice, formatSat } from '@/common/utils'
import { formatMr2 } from '../perp/utils'
import { formatNumberToKMBT } from '@/common/utils'
import FutureLib from '@/lib/future'
import { connect } from 'umi'
import styles from './data.less'
import { useEffect, useMemo, useState } from 'react'

function PairData(props) {
  const {
    longFundingRate,
    shortFundingRate,
    totalShortSize,
    totalLongSize,
    userIndexPrice,
    userIndexTokenDecimal,
    collateralToken,
    userCollateralTokenDecimal,
  } = props.data
  const totalSize = (size: bigint) => {
    if (!size) return 0
    const total = formatSat(size.toString(), userIndexTokenDecimal)
    return calUsdPrice(total, userIndexPrice)
  }

  const [insuranceFunds, setInsuranceFunds] = useState('-')
  useEffect(() => {
    //
    const getInsuranceFunds = async () => {
      const res = await FutureLib.collateralInsuranceFunds(props.chainId, collateralToken)

      const total = formatSat(res.toString(), userCollateralTokenDecimal)
      const result = formatNumberToKMBT(Number(total))
      console.log(collateralToken, props.chainId, res, result, 'collateralToken,props.chainIds')
      setInsuranceFunds(result)
    }
    if (props.chainId && collateralToken) {
      getInsuranceFunds()
    }
  }, [collateralToken, props.chainId, userCollateralTokenDecimal])

  const totalLongSizeKMB = useMemo(() => {
    return formatNumberToKMBT(Number(totalSize(totalLongSize || 0)))
  }, [totalLongSize])
  const totalShortSizeKMB = useMemo(() => {
    return formatNumberToKMBT(Number(totalSize(totalShortSize || 0)))
  }, [totalShortSize])
  console.log(props, 'data------')
  if (!userIndexTokenDecimal) return null
  return (
    <div className={styles.wrap}>
      <div className={styles.item}>
        <div className={styles.title}>Mark: </div>
        <div className={styles.desc}>Spot: </div>
      </div>
      <div className={styles.item}>
        <div className={styles.title}> --</div>
        <div className={styles.desc}>24H Volume</div>
      </div>
      <div className={styles.item}>
        <div className={styles.title}>-- </div>
        <div className={styles.desc}>24H Fee </div>
      </div>
      <div className={styles.item}>
        <div className={styles.long}>
          L ${totalLongSizeKMB} | {formatMr2(longFundingRate || 0)}
        </div>
        <div className={styles.short}>
          S ${totalShortSizeKMB} | {formatMr2(shortFundingRate || 0)}
        </div>
      </div>
      {/* <div className={styles.item}>
        <div className={styles.title}>-- </div>
        <div className={styles.desc}>APY </div>
      </div> */}
      <div className={styles.item}>
        <div className={styles.title}> ${insuranceFunds}</div>
        <div className={styles.desc}>Insurance Pool </div>
      </div>
    </div>
  )
}

export default connect(({ wallet }) => ({
  chainId: wallet.walletChainId,
}))(PairData)
