import _ from '@/i18n'
import styles from './info.less'
import type { FutureStateType } from '@/models/dataType'
import { connect } from 'umi'
import { useEffect, useState } from 'react'
import { amountRaw, formatSat } from '@/common/utils'
import {
  opIncreaseMargin,
  opDecreaseMargin,
  calcMr,
  calcIndexLiqPrice,
} from '@1kx/typechain/helper/future'
import { formatBnPrice, formatLeverage, formatMr } from '../perp/utils'
import { ArrowRight } from '@/components/ui'
type propsType = {
  currentToken: any
  amountIn: string
  isAdd: boolean
}
type dataType = {
  oldLeverage: string
  newLeverage: string
  oldMr: string
  newMr: string
  mmr: string
  oldMargin: string
  newMargin: string | number
  newLiqPrice: string
}

function Info(props: propsType) {
  const { currentToken, amountIn, isAdd } = props
  const { margin, pair, openNotional, userLiqPrice } = currentToken

  const [data, setData] = useState<dataType>({} as dataType)

  useEffect(() => {
    const amountInRaw = amountRaw(amountIn || '0', pair.userCollateralTokenDecimal)
    const params = [currentToken, pair, BigInt(amountInRaw), pair.collateralPrice, pair.indexPrice]
    let res = isAdd ? opIncreaseMargin(...params) : opDecreaseMargin(...params)

    const oldLeverage = formatLeverage(margin, openNotional)
    let newLeverage = formatLeverage(res.pos.margin, res.pos.openNotional)

    const oldMr = formatMr(calcMr(currentToken, pair, pair.collateralPrice, pair.indexPrice))
    let newMr = formatMr(res.mr)

    const mmr = formatMr(res.pos.mmr)

    const oldMargin = formatSat(margin.toString(), pair.userCollateralTokenDecimal)
    let newMargin = formatSat(res.pos.margin.toString(), pair.userCollateralTokenDecimal)

    let newLiqPrice = formatBnPrice(
      calcIndexLiqPrice(res.pos, res.pair, res.pair.collateralPrice, res.pair.indexPrice),
      4,
    )
    if (!amountIn) {
      newLeverage = oldLeverage
      newMr = oldMr
      newMargin = oldMargin
      newLiqPrice = userLiqPrice
    }

    setData({
      oldLeverage,
      newLeverage,
      oldMr,
      newMr,
      mmr,
      oldMargin,
      newMargin,
      newLiqPrice,
    })
  }, [currentToken, amountIn])

  return (
    <div className={styles.info}>
      <div className={styles.item}>
        <div className={styles.label}>{_('leverage')}</div>
        <div className={styles.value}>
          {data.oldLeverage} <ArrowRight /> {data.newLeverage}
        </div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>{_('margin_rate')}</div>
        <div className={styles.value}>
          {data.oldMr} <ArrowRight /> {data.newMr}
        </div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>{_('maintenance_margin_rate')}</div>
        <div className={styles.value}>{data.mmr}</div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>EST {_('liq_price')}</div>
        <div className={styles.value}>
          ${userLiqPrice} <ArrowRight /> ${data.newLiqPrice}
        </div>
      </div>
    </div>
  )
}

const mapStateToProps = ({ future }: { future: FutureStateType }) => {
  return {
    ...future,
  }
}

export default connect(mapStateToProps)(Info)
