import styles from './index.less'
import Tradeview from '@/components/tradeview'
import type {
  CommonStateType,
  TokenDataType,
  TokenStateType,
  WalletStateType,
} from '@/models/dataType'
import { connect,useDispatch } from 'umi'
import ChooseToken from '@/components/chooseToken/base'
import { useEffect, useState } from 'react'
import { Loading } from '@/components/loading'
import PairData from './data'

function Chart(props: CommonStateType & TokenStateType & WalletStateType) {
  const dispatch = useDispatch()
  const { isDarkMode, tokenDatas, chartSelectedTokenId, isLogin, chartSelectedPair, loadError } =
    props
  console.log(chartSelectedPair,'chartSelectedPair')
  const [currentToken, setCurrentToken] = useState<TokenDataType>({} as TokenDataType)

  const onChangeToken = (token: TokenDataType) => {
    // setCurrentToken(token)
    dispatch({
      type: 'tokens/save',
      payload: {
        chartSelectedTokenId: token.id,
        chartSelectedPair: {},
      },
    })
  }
  // useEffect(() => {
  //   console.log(currentToken.symbol && tokenDatas && tokenDatas[1])
  //   if (!currentToken.symbol && tokenDatas && tokenDatas[1]) setCurrentToken(tokenDatas[1])
  // }, [currentToken.symbol, tokenDatas])

  useEffect(() => {
    if (chartSelectedTokenId) {
      const _token = tokenDatas.find((v) => v.id === chartSelectedTokenId)
      if (_token) {
        setCurrentToken(_token)
      }
    } else if (tokenDatas && tokenDatas[0]) {
      // debugger
      setCurrentToken(tokenDatas[0])
    }
  }, [chartSelectedTokenId, tokenDatas, tokenDatas && tokenDatas.length])

  return (
    <div className={`panel ${styles.container}`}>
      {tokenDatas && !currentToken.symbol && !loadError ? (
        <div className={styles.loading}>
          <Loading />
        </div>
      ) : (
        <>
          <div className={styles.head}>
            <ChooseToken
              currentToken={currentToken}
              changeToken={onChangeToken}
              data={(tokenDatas || []).filter((v) => v.symbol && v.symbol.toLowerCase() !== 'osd')}
              className={styles.token}
              loading={tokenDatas && tokenDatas.length < 1}
              isLogin={isLogin}
            />
            {chartSelectedPair && <PairData data={chartSelectedPair} />}
          </div>
          {currentToken.symbol && (
            <Tradeview theme={isDarkMode ? 'Dark' : 'Light'} symbol={currentToken.symbol} />
          )}
        </>
      )}
    </div>
  )
}

const mapStateToProps = ({
  common,
  tokens,
  wallet,
}: {
  common: CommonStateType
  tokens: TokenStateType
  wallet: WalletStateType
}) => {
  return {
    ...common,
    ...tokens,
    ...wallet,
  }
}

export default connect(mapStateToProps)(Chart)
