import { Modal } from 'antd'
import { useEffect, useState } from 'react'
import { DownloadArr } from '../ui'
import styles from './index.less'
import _ from '@/i18n'
import Token from './token'
import USD from '@/components/price/usd'
import SearchInput from '../search/base'
import type { TokenDataType } from '@/models/dataType'
import DefultIcon from '../icon/defult'
import { Loading as Spin } from '@/components/loading'
type propsType = {
  changeToken: (token: TokenDataType) => void
  data: TokenDataType[]
  currentToken: TokenDataType
  isLogin: boolean
  loading: boolean
  className: string
}
function ChooseToken(props: propsType) {
  const { changeToken, data = [], currentToken, isLogin, loading, className } = props
  const [modalVisible, setModalVisible] = useState(false)
  const [initList, setInitList] = useState(data)
  const [list, setList] = useState<TokenDataType[] | undefined>(undefined)
  let currentTokenIndex: number | undefined = undefined
  if (currentToken && currentToken.tokenId)
    currentTokenIndex = data.findIndex((v) => v.tokenId === currentToken.tokenId)

  // console.log('currentTokenIndex:', currentTokenIndex)
  // console.log('data', data)

  useEffect(() => {
    if (data.length > 0) setInitList(data)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data, isLogin])

  const choose = (item: TokenDataType) => {
    setModalVisible(false)
    changeToken(item)
  }
  return (
    <>
      {typeof currentTokenIndex === 'undefined' ? (
        <div
          className={`${styles.token} ${styles.noToken} ${className}`}
          onClick={() => setModalVisible(true)}
        >
          <div className={styles.noData}>{_('select_asset')}</div>
          <DownloadArr />
        </div>
      ) : (
        <Token
          className={className}
          changeToken={() => setModalVisible(true)}
          data={currentToken}
        />
      )}

      <Modal
        width={420}
        footer={null}
        open={modalVisible}
        getContainer={() => document.getElementById('J_Page') || document.body}
        onCancel={() => setModalVisible(false)}
        closable={false}
        wrapClassName={styles.modal}
      >
        <Spin spinning={loading}>
          <div className={styles.container}>
            <div className={styles.search_wrap}>
              <SearchInput
                changeShowList={(newlist: TokenDataType[]) => setList(newlist)}
                data={data}
              />
            </div>
            <div className={styles.content}>
              {(list || initList).map((item, index) => {
                const { symbol, fullName, icon, iconLp, tokenId, userTokenBalance } = item
                const balance = userTokenBalance
                return (
                  <div
                    className={
                      currentTokenIndex === index
                        ? `${styles.item} ${styles[`no${index}`]} ${styles.selected_item}`
                        : `${styles.item} ${styles[`no${index}`]}`
                    }
                    key={tokenId}
                    onClick={() => choose(item)}
                  >
                    <div className={styles.info}>
                      <div className={styles.icon}>
                        {iconLp || icon ? (
                          <img src={icon} />
                        ) : (
                          <DefultIcon tokenId={tokenId} size={25} />
                        )}
                      </div>
                      <div className={styles.title}>
                        <div className={styles.symbol}>{symbol}</div>
                        <div className={styles.full_name}>{fullName}</div>
                      </div>
                    </div>
                    <div className={styles.balance}>
                      <div className={styles.no}>{balance}</div>
                      <div className={styles.usd}>
                        <USD data={{ amount: Number(balance), token: item }} />
                      </div>
                    </div>
                  </div>
                )
              })}
            </div>
          </div>
        </Spin>
      </Modal>
    </>
  )
}

export default ChooseToken
