import { Loading as Spin } from '@/components/loading'
import styles from './closePosition.less'
import _ from '@/i18n'
import { useState } from 'react'
import Slider from '@/components/slider'
import Btn from '@/components/btn'
import Info from './info'
import { amountRaw, calUsdPrice } from '@/common/utils'
import { InputNumber } from 'antd'
import Token from '@/components/chooseToken/token'
import { useDispatch, connect } from 'umi'
import { Result } from '@/components/result/simple'
import { message } from '@/components/message'
import BN from 'bignumber.js'
import type { FutureStateType } from '@/models/dataType'
import { opDecreasePosition } from '@1kx/typechain/helper/future'
import Icon from '@/components/icon'

function ClosePostion(props) {
  const dispatch = useDispatch()
  const { currentToken, loading, closeModal } = props
  const {
    userSize: size,
    collateralTokenObj,
    indexTokenObj,
    isLong,
    userMargin: margin,
    pair,
  } = currentToken
  const [submiting, setSubmiting] = useState(false)
  const [amountIn, setAmountIn] = useState(0)

  const changeAmount = (value: number) => {
    setAmountIn(value)
  }

  const opReturn = (amountInRaw) => {
    const data = opDecreasePosition(
      currentToken,
      pair,
      BigInt(amountInRaw),
      pair.collateralPrice,
      pair.indexPrice,
    )
    console.log('data:', data)
    return data
  }

  const handleSubmit = async () => {
    setSubmiting(true)
    console.log(amountIn, size)
    const amountInRaw = amountRaw(amountIn.toString(), pair.userIndexTokenDecimal)

    const _data = opReturn(amountInRaw)

    try {
      const res = await dispatch({
        type: 'future/decreasePosition',
        payload: {
          tokenId: collateralTokenObj.id,
          params: {
            _collateralToken: collateralTokenObj.id,
            _indexToken: indexTokenObj.id,
            _isLong: isLong,
            _marginDelta:
              amountIn === size
                ? 0
                : new BN(amountIn)
                  .div(size)
                  .multipliedBy(margin)
                  .multipliedBy(Math.pow(10, pair.userCollateralTokenDecimal))
                  .toFixed(0),
            // _notionalDelta: new BN(amountIn)
            //   .div(size)
            //   .multipliedBy(openNotional)
            //   .multipliedBy(Math.pow(10, collateralTokenObj.tokenDecimal))
            //   .toFixed(0),
            _notionalDelta: _data.notionalDelta,
          },
        },
      })
      if (res.hash) {
        Result({
          msg: isLong ? (
            <div className={`${styles.result_icon} ${styles.green}`}>
              <Icon type="icon-up" />
              {_('long_closed')}
            </div>
          ) : (
            <div className={`${styles.result_icon} ${styles.red}`}>
              <Icon type="icon-down1" />
              {_('short_closed')}
            </div>
          ),
          txid: res.hash,
          token: indexTokenObj,
          chainId: props.wallet.walletChainId,
          onOk: () => {
            closeModal()
            dispatch({
              type: 'future/getPairs',
            })
          },
        })

        dispatch({
          type: 'future/getPositionList',
        })
      }
    } catch (error: any) {
      message(error)
    }
    setSubmiting(false)
  }

  return (
    <Spin spinning={loading || submiting}>
      <div className={styles.container}>
        <div className={styles.card}>
          <div className={styles.label}>
            <div>
              {_('close')} {isLong ? _('long') : _('short')}: $
              {calUsdPrice(amountIn, pair.userIndexPrice)}
            </div>
            <div>
              {_('position_size')}:&nbsp;
              <span className="primary" onClick={() => changeAmount(size)}>
                {size}
              </span>
            </div>
          </div>
          <div className={styles.value}>
            <InputNumber
              className="inputTxt"
              value={amountIn}
              onChange={changeAmount}
              min={0}
              max={size}
            />
            <Token data={indexTokenObj} />
          </div>
          <Slider amount={amountIn} onChange={changeAmount} max={size} />
        </div>
        <div className={styles.card}>
          <div className={styles.label}>
            <div>{_('price')}</div>
            <div>
              {_('mark')}:{pair.userIndexPrice}
            </div>
          </div>
          <div className={styles.value}>
            <div>{pair.userIndexPrice}</div>
            <div>USD</div>
          </div>
        </div>
        <Info currentToken={currentToken} amountIn={amountIn} />
        <Btn
          height={50}
          align="center"
          onClick={handleSubmit}
          conditions={[
            {
              handle: amountIn <= 0,
              txt: _('input_amount'),
            },
            {
              handle: amountIn > parseFloat(size || '0'),
              txt: _('lack_balance'),
            },
          ]}
        >
          {_('close_position')}
        </Btn>
      </div>
    </Spin>
  )
}

const mapStateToProps = ({ future, wallet }: { future: FutureStateType, wallet: any }) => {
  return {
    ...future,
    wallet
  }
}

export default connect(mapStateToProps)(ClosePostion)
