import type { BigNumberish } from 'ethers'
import { BigNumber } from 'ethers'

export function e(num: BigNumberish, exp: BigNumberish) {
  return BigNumber.from(10).pow(exp).mul(num)
}

export function amountSlipMin(amount: string, slip: string) {
  const slipNum = Math.round(+slip * 10000)
  const min = BigNumber.from(amount).sub(
    BigNumber.from(amount)
      .mul(slipNum)
      .div(100 * 10000),
  )
  return min.toString()
}

export function amountSlipMax(amount: string, slip: string) {
  const slipNum = Math.round(+slip * 10000)
  const max = BigNumber.from(amount).add(
    BigNumber.from(amount)
      .mul(slipNum)
      .div(100 * 10000),
  )
  return max.toString()
}
