
import { Table, Spin } from 'antd'
import _ from '@/i18n'
import React, { useEffect, useState, useMemo } from 'react'
import { connect, useDispatch } from 'umi'
import { formatDate } from '@/common/utils'
import type { FuturePair } from '@1kx/typechain/helper/future'
import FutureLib from '@/lib/future'
import styles from './index.less'

// useEffect
type Props = {
  list: API.FutureLimitItem[],
  account: string,
  network: 'zk' | 'sepolia'|'arbitrum' | null,
  loading: boolean,
  tokenPairs: FuturePair[],
  chainId: string,
  tokens: any,

}


const LimitOrder: React.FC<Props> = ({ list = [], account = '', network = '', loading , chainId, tokenPairs = [], tokens }) => {
  

  const tokenPairsReday = useMemo(() => {
    return tokenPairs.length > 0
  }, [tokenPairs])
  const tokensReday = useMemo(() => {
    return tokens.tokenDatas.length > 0
  }, [tokens])

  const dispatch = useDispatch();
  useEffect(() => {
    if (account && tokenPairsReday && tokensReday) {
      console.log('wtffff')
      dispatch({
        type: 'future/fetchLimitOrder',
        payload: { account, network },
        callback: (ret) => {
          if (ret.error) {
            console.log(ret)
          }
        }
      })
    }
  }, [account, network, tokenPairsReday, tokensReday, dispatch])

  const handleCancel = async (record: API.FutureLimitItem) => {
    // 
    // 
    //    loading
    dispatch({
      type: 'future/save',
      payload: {
        limitOrder: list.map(item => {
          item.submiting = item.id === record.id
          return item
        })
      },
    })
    try {
      await FutureLib.cancelIncreaseOrder(chainId, BigInt(record.orderIndex))
    } catch (err) {
      console.log(err)
    }


    dispatch({
      type: 'future/fetchLimitOrder',
      payload: { account, network },
      callback: (ret) => {
        if (ret.error) {
          console.log(ret)
        }
      }
    })
  }

  const handleCancelAll = () => {
    // 
    //  
    dispatch({
      type: 'future/fetchLimitOrder',
      payload: { account, network },
      callback: (ret) => {
        if (ret.error) {
          console.log(ret)
        }
      }
    })
  }


  const columns = [
    {
      title: 'Time',
      dataIndex: 'timestamp',
      key: 'timestamp',
      render: (text) => formatDate(text * 1000, 'MM/DD, HH:mm:ss'),
      align: 'right'
    },

    {
      title: 'Positions',
      dataIndex: 'execStatusName',
      key: 'execStatusName',
      align: 'right',
      render: (text, record: API.FutureLimitItem) => {
        return <>
          {
            record.execStatus === 3 && <><span className={record.isLong ? styles.longText : styles.shortText}>{record.isLong ? 'L' : 'S'}</span> <span>{record.indexSymbol}</span></>
          }
        </>
      },
    },
    {
      title: 'Size',
      dataIndex: 'notionalDeltaForUSD',
      key: 'notionalDeltaForUSD',
      render: (text) => `$${text}`,
      align: 'right'
    },
    {
      title: <div className={styles.tableTh}>
        <span>
          Margin
        </span>
        <span>
          Value
        </span>
      </div>,
      dataIndex: 'Margin',
      key: 'Margin',
      render: (text,record) => (<div className={styles.tableTh}>
        <span>
          {text} {record.collateralSymbol}
        </span>
        <span>
         ${record.MarginValue}
        </span>
      </div>),
      align: 'right'
    },
    {
      title: 'Price',
      dataIndex: 'targetPrice',
      key: 'targetPrice',
      align: 'right',
      render: (text, record: API.FutureLimitItem) => {
        return <>
          {
            record.targetPrice ? `$${record.targetPrice}` : `$${record.maxPrice}-$${record.minPrice}`

          }

        </>
      },
    },
    {
      title: () => (<span   >Manage</span>),
      key: 'action',
      align: 'right',
      render: (_, record) => {
        console.log(record.submiting)
        return <Spin spinning={Boolean(record.submiting)} size="small"><span className={styles.editText} onClick={() => { handleCancel(record) }}>Cancel</span></Spin>
      },
    },

  ]

  return (
    <div className={styles.container} >
      <div >
        <Table
          columns={columns}
          dataSource={list}
          size="small"
          loading={loading}

          rowKey="id"
        />
      </div>
    </div>
  )
}



export default connect(({ future, wallet, tokens, loading }) => ({
  list: future.limitOrder,
  tokenPairs: future.tokenPairs, //
  account: wallet.account, //
  network: wallet.currentWallet && wallet.currentWallet.network, //
  chainId: wallet.walletChainId,
  tokens: tokens, // indexToken  coin name
  loading: loading.effects['future/fetchLimitOrder'] ,
}))(LimitOrder)
