import _ from '@/i18n'
import { Input } from 'antd'
import { SearchOutlined } from '@ant-design/icons'

const escapeRegExpWildcards = (searchStr: string) => {
  const regExp = /([\(\[\{\\\^\$\}\]\)\?\*\+\.])/gim
  if (searchStr && regExp.test(searchStr)) {
    return searchStr.replace(regExp, '\\$1')
  }
  return searchStr
}

const searchByKeywords = (keywords: string, searchArr) => {
  const keywordsExp = new RegExp('.*?' + escapeRegExpWildcards(keywords) + '.*?', 'img')

  return searchArr.filter((v) => {
    return keywordsExp.test(v.symbol) || keywords == v.tokenId || keywords == v.liquidity
  })
}

function SearchInput(props) {
  const { changeShowList, data } = props

  const handleChange = (e) => {
    const { value } = e.target

    if (!value) {
      return changeShowList(data)
    }
    const searchResultList = searchByKeywords(value, data)
    changeShowList(searchResultList)
  }

  return (
    <Input
      placeholder={_('search_tips')}
      onChange={handleChange}
      className="search_input"
      allowClear={true}
      prefix={<SearchOutlined />}
    />
  )
}

export default SearchInput
