import _ from '@/i18n'
import styles from './index.less'
import Btn from '@/components/btn'
import { DoubleArrCircle,SwapDownArr } from '@/components/ui'
import {
  amountRaw,
  debounce,
  envir,
  formatSat,
  getTokenDataById,
  // newSetInterval,
  toFixed,
} from '@/common/utils'
import { connect, useDispatch } from 'umi'
import type { CommonStateType, TokenStateType, WalletStateType } from '@/models/dataType'
import { useEffect, useState } from 'react'
import { amountSlipMax, amountSlipMin } from '@/common/etherUtils'
import { message } from '@/components/message'
import { Loading as Spin } from '@/components/loading'
import SimpleCard from '@/components/card'
import Result from '@/components/result'
import { AmountLoading } from '@/common/config'
import BN from 'bignumber.js'

function Swap(props: {
  tokenDatas: any
  walletChainId: any
  dataSourceChainId: any
  account: any
  [key: string]: any
}) {
  const {
    walletChainId,
    tokenDatas,
    currentWallet,
    osdTokenData,
    slippage,
    isLogin,
    swapTokenId,
    loadingData,
    loadError,
  } = props
  const dispatch = useDispatch()

  const [tokenIn, setTokenIn] = useState('')
  const [tokenOut, setTokenOut] = useState(swapTokenId)
  const [amountIn, setAmountIn] = useState('')
  const [amountOut, setAmountOut] = useState('')
  const [amountType, setAmountType] = useState('')
  const [priceImpact, setPriceImpact] = useState('0')
  const [txid, setTxid] = useState('')
  const [total, setTotal] = useState(0)
  const [submiting, setSubmiting] = useState(false)
  const [calculating, setCalculating] = useState('')
  const [resultVisible, setResultVisible] = useState(false)
  const [varA, setVarA] = useState(0)

  const tokenInObj = getTokenDataById(tokenIn, tokenDatas ? [...tokenDatas, osdTokenData] : [])
  const tokenOutObj = getTokenDataById(tokenOut, tokenDatas ? [...tokenDatas, osdTokenData] : [])

 

  const { getTokenPrice, calcSwapAmountIn, swap, calcSwapAmountOut, calcPoolTotal } =
    currentWallet || {}

  useEffect(() => {
    async function fun() {
      if (amountType === 'in' && amountIn) {
        changeAmountIn(amountIn)
      } else if (amountType === 'out' && amountOut) {
        changeAmountOut(amountOut)
      }

      if (!currentWallet) return
      let _total = await calcPoolTotal(tokenInObj, tokenOutObj)

      setTotal(_total)
    }
    if (tokenOut && tokenIn) fun()
  }, [tokenOut, tokenIn, isLogin])

  const closeResultModal = () => {
    setResultVisible(false)
    setAmountIn('')
    setAmountOut('')
    setTxid('')
    setPriceImpact('0')
    dispatch({
      type: 'tokens/updatePrice',
    })
  }

  const finishSubmit = (hash) => {
    setTxid(hash)
    setResultVisible(true)
    dispatch({
      type: 'tokens/updatePrice',
    })
  }

  const handleSubmit = async () => {
    clearTimeout(varA)
    const env = await envir(currentWallet, walletChainId)
    if (!env) return
    console.log(currentWallet,'currentWallet')
    setSubmiting(true)

    const amountInRaw = amountRaw(amountIn, tokenInObj.tokenDecimal)
    const amountOutRaw = amountRaw(amountOut, tokenOutObj.tokenDecimal)
    const amountInRawMax = amountSlipMax(amountInRaw, slippage)
    const amountOutRawMin = amountSlipMin(amountOutRaw, slippage)
    const res = await swap({
      chainId: walletChainId,
      tokenIn,
      amountInRaw,
      amountInRawMax,
      tokenOut,
      amountOutRaw,
      amountOutRawMin,
      type: amountType,
    })
    // console.log(res)
    if (res.error) {
      message(res.error, {
        tokenOut: tokenOutObj.symbol,
        tokenIn: tokenInObj.symbol,
      })
    } else if (res.hash) {
      finishSubmit(res.hash)
    }
    setSubmiting(false)
  }

  async function calImpact(_amountIn: string, _amountOut: string) {
    if (!_amountIn || !_amountOut) {
      return '0'
    }
    const tokenIn_price = await getTokenPrice(
      tokenIn,
      walletChainId,
      tokenInObj.tokenDecimal,
      tokenInObj,
    )
    const tokenOut_price = await getTokenPrice(
      tokenOut,
      walletChainId,
      tokenOutObj.tokenDecimal,
      tokenOutObj,
    )
    if (!tokenIn_price || !tokenOut_price) return '0'
    const zeroImpactToken2Amount = new BN(_amountIn).multipliedBy(tokenIn_price).div(tokenOut_price)
    const impact = zeroImpactToken2Amount
      .minus(_amountOut)
      .div(zeroImpactToken2Amount)
      .multipliedBy(100)
      .abs()
    return zeroImpactToken2Amount.eq(0) ? 0 : toFixed(impact.toString(), 2)
  }

  async function changeAmountInOut(type: 'in' | 'out', amount: string = '0') {
    // if (calculating) return
    setAmountType(type)

    if (type === 'in') {
      setAmountIn(amount)
    } else {
      setAmountOut(amount)
    }
    const env = await envir(currentWallet, walletChainId)
    if (!env || !tokenIn || !tokenOut) return

    debounce(type === 'in' ? changeAmountIn : changeAmountOut, 500)(amount)
  }

  async function calcAmountIn(amount: string, amountInRaw: string) {
    const _out = await calcSwapAmountOut(walletChainId, amountInRaw, tokenIn, tokenOut)
    // console.log('_out', _out)
    if (_out.error) {
      message(_out.error, {
        tokenOut: tokenOutObj.symbol,
        tokenIn: tokenInObj.symbol,
      })
      setAmountOut('')
      setPriceImpact('0')
    } else {
      const _amountOut = formatSat(_out.toString(), tokenOutObj.tokenDecimal, 0)
      // console.log('_amountOut', _amountOut)
      setAmountOut(_amountOut)
      const impact = await calImpact(amount, _amountOut)
      // console.log('impact', impact)
      setPriceImpact(impact)
    }
  }

  async function changeAmountIn(amount: string) {
    clearTimeout(varA)
    const env = await envir(currentWallet, walletChainId)
    if (!env) return

    if (!amount) {
      setAmountOut('')
      setPriceImpact('0')
      return
    }
    setCalculating('in')

    const _amountInRaw = amountRaw(amount, tokenInObj.tokenDecimal)
    await calcAmountIn(amount, _amountInRaw)

    setCalculating('')

    // let timerA = newSetInterval(async () => {
    //   // console.log(varA)
    //   await calcAmountIn(amount, _amountInRaw)
    // }, 3 * 1e3)
    // setVarA(timerA)
  }

  async function calcAmountOut(amount: string, amountOutRaw: string) {
    const _in = await calcSwapAmountIn(walletChainId, amountOutRaw, tokenIn, tokenOut)
    // console.log('_out', _in)
    if (_in.error) {
      message(_in.error, {
        tokenOut: tokenOutObj.symbol,
        tokenIn: tokenInObj.symbol,
      })
      setAmountIn('')
      setPriceImpact('0')
    } else {
      const _amountIn = formatSat(_in.toString(), tokenInObj.tokenDecimal, 0)
      // console.log('_amountIn', _amountIn)
      setAmountIn(_amountIn)
      const impact = await calImpact(_amountIn, amount)
      setPriceImpact(impact)
    }
  }

  async function changeAmountOut(amount: string) {
    clearTimeout(varA)
    if (!amount) {
      setAmountIn('')
      setPriceImpact('0')
      return
    }
    setCalculating('out')

    const _amountOutRaw = amountRaw(amount, tokenOutObj.tokenDecimal)
    await calcAmountOut(amount, _amountOutRaw)

    setCalculating('')
    // let timerA = newSetInterval(async () => {
    //   await calcAmountOut(amount, _amountOutRaw)
    // }, 3 * 1e3)
    // setVarA(timerA)
  }
  const reverse = () => {
    let tmpIn = tokenIn
    clearTimeout(varA)
    setTokenIn(tokenOut)
    setTokenOut(tmpIn)
  }

  const beyond_slip = parseFloat(priceImpact) > parseFloat(slippage)

  return (
    <>
      <Spin
        spinning={(tokenDatas && tokenDatas.length < 1 && !loadError) || loadingData || submiting}
      >
        <div className={styles.container}>
          {/* <div className={`primary ${styles.title}`}>{_('market')}</div> */}
          <SimpleCard
            className={styles.cardPay}
            title={_('pay')}
            amount={calculating === 'out' ? AmountLoading : amountIn}
            onAmountChange={(amount: number) => changeAmountInOut('in', (amount || '0').toString())}
            token={tokenInObj}
            onChangeToken={(item) => {
              if (item.tokenId === tokenOut) reverse()
              setTokenIn(item.tokenId)
            }}
          />
          <div className={styles.arr} onClick={reverse}>
            <SwapDownArr className={styles.arrCircle} />
          </div>
          <SimpleCard
            className={styles.cardReceive}
            amount={calculating === 'in' ? AmountLoading : amountOut}
            onAmountChange={(amount: number) =>
              changeAmountInOut('out', (amount || '0').toString())
            }
            token={tokenOutObj}
            title={_('receive')}
            onChangeToken={(item) => {
              if (item.tokenId === tokenIn) reverse()
              setTokenOut(item.tokenId)
              dispatch({
                type: 'tokens/save',
                payload: {
                  chartSelectedTokenId: item.tokenId,
                  chartSelectedPair: {},
                },
              })
            }}
          />
          <div className={styles.info}>
            <div className={styles.item}>
              <div className={styles.label}>
                {_('price_impact')} + {_('fee')}
              </div>
              <div className={beyond_slip ? `${styles.fee} ${styles.red}` : styles.fee}>
                {priceImpact}%
              </div>
            </div>
          </div>

          <Btn
            height={50}
            align="center"
            onClick={handleSubmit}
            redCond={beyond_slip}
            conditions={[
              {
                handle: !tokenIn || !tokenOut,
                txt: _('select_asset'),
              },
              {
                handle: total > 0 && parseFloat(amountOut) > total,
                txt: _('not_enough', tokenOutObj.symbol),
              },
              {
                handle: parseFloat(amountIn || '0') <= 0 || parseFloat(amountOut || '0') <= 0,
                txt: _('input_amount'),
              },
              // {
              //   handle:
              //     parseFloat(amountIn || '0') > parseFloat(tokenInObj.userTokenBalance || '0'),
              //   txt: _('lack_balance'),
              // },
            ]}
          >
            {beyond_slip ? _('swap_anyway') : _('swap')}
          </Btn>
        </div>
      </Spin>
      {resultVisible && (
        <Result
          title={_('swap_done')}
          amountTile={_('swap_amount')}
          txid={txid}
          visible={resultVisible}
          close={closeResultModal}
          chainId={walletChainId}
          tokenIn={{
            ...tokenInObj,
            amount: amountIn,
          }}
          tokenOuts={[{ ...tokenOutObj, amount: amountOut }]}
        />
      )}
    </>
  )
}

const mapStateToProps = ({
  common,
  tokens,
  wallet,
  loading,
}: {
  common: CommonStateType
  tokens: TokenStateType
  wallet: WalletStateType
  loading: any
}) => {
  const effects = loading.effects
  return {
    ...common,
    ...tokens,
    ...wallet,
    loadingData: effects['tokens/getData'],
  }
}

export default connect(mapStateToProps)(Swap)
