import styles from './index.less'
import _ from '@/i18n'
import { InputNumber, message, Modal, Col } from 'antd'
import { CheckCircleOutlined } from '@ant-design/icons';
import { useEffect, useMemo, useState } from 'react'
import ChooseToken from '@/components/chooseToken/base'
import { connect, useDispatch } from 'umi'
import BN from 'bignumber.js'
import type {
  FutureStateType,
  TokenDataFullType,
  TokenDataType,
  TokenStateType,
  WalletStateType,
} from '@/models/dataType'
import Btn from '@/components/btn'
import TPSL from '../tpsl'
import { amountRaw, calUsdPrice, debounce, envir, formatSat } from '@/common/utils'
import FutureLib from '@/lib/future'
// import { message } from '@/components/message'
import { Result } from '@/components/result/simple'
import { Loading as Spin } from '@/components/loading'
import Icon from '@/components/icon'
import {
  opIncreasePosition,
  calcOpIncreasePosition,
  LEVERAGE_PRECISION,
  calcLimitIncreaseSizeDelta,
  calcLimitDecreaseSizeDelta,
  TRADING_FEE_RATE_PRECISION,
  FUNDING_RATE_PRECISION
} from '@1kx/typechain/helper/future'
import { findPair, formatBnPrice, formatPriceToBN } from './utils'
import type { Future } from '@1kx/typechain'
import { OsdTokenData } from '@/common/config'
import { getChainById } from '@/layout/header/chainData'


type PositionType = {
  collateralToken: string;
  indexToken: string;
  isLong: boolean;
  margin: bigint;
  openNotional: bigint;
  size: bigint;

  entryFundingRate: bigint;
  entryCollateralPrice: bigint;
  entryIndexPrice: bigint;
  mmr: bigint;
}

const findToken = (_tokenId: string, tokenDatas: any[]) => {
  if (!_tokenId) return {}
  if (!tokenDatas) return {}
  console.log(tokenDatas)
  const _token = tokenDatas.find(
    (t) => t.id && t.id.toLowerCase() === _tokenId.toLowerCase(),
  )
  return _token || {}
}

type DescriptionProps = {
  label: string,
  value: string | number
}

const Description: React.FC<DescriptionProps> = ({ label, value }) => (<Col span={24}>
  <div className={styles.DescriptionItem}>
    <p className={styles.DescriptionItemLabel} >
      {label}:
    </p>
    <p className={styles.DescriptionItemValue}  >
      {value || '--'}
    </p>

  </div>
</Col>)

function Perp(props: any) {
  const dispatch = useDispatch()
  const {
    isLogin,
    tokenDatas,
    osdTokenData,
    tokenPairs,
    isLong,
    currentWallet,
    walletChainId,
    positionList,
  } = props

  const [amountIn, setAmountIn] = useState<number | null>(null)
  const [tokenIn, setTokenIn] = useState('')
  const [amountOut, setAmountOut] = useState<number | null>(null)
  const [tokenOut, setTokenOut] = useState('')
  const [submiting, setSubmiting] = useState(false)
  const [indexTokenData, setIndexTokenData] = useState<TokenDataType[]>([])
  const [collsTokenData, setCollsTokenData] = useState<TokenDataType[]>([])
  const [leverage, setLeverage] = useState(1.0)
  const [type, setType] = useState('')
  const [tradeType, setTradeType] = useState<'market' | 'limit'>('market')

  const [curPair, setCurPair] = useState(findPair(tokenIn, tokenOut, tokenPairs))
  const [curPos, setCurPos] = useState({})
  const [curData, setCurData] = useState({})

  const [targetPrice, setTargetPrice] = useState(0);
  const [tpPrice, setTpPrice] = useState(0);
  const [slPrice, setSlPrice] = useState(0);
  const [tpsl, setTPSL] = useState(false);

  const tokenInObj = findToken(tokenIn, [...tokenDatas, osdTokenData])
  const tokenOutObj = findToken(tokenOut, [...tokenDatas, osdTokenData])



  //  
  useEffect(() => {
    if (tokenDatas && tokenDatas.length > 0) {
      dispatch({
        type: 'future/getPairs',
      })
    }
  }, [tokenDatas, dispatch])

  // 

  useEffect(() => {
    let indexTokenList: TokenDataType[] = []
    if (tokenPairs && tokenDatas.length)
      tokenPairs.forEach((v) => {
        const id = indexTokenList.findIndex(
          (c) => c.id && c.id.toLowerCase() === v.indexToken.toLowerCase(),
        )
        if (id < 0) {
          const _token = findToken(v.indexToken, [...tokenDatas, OsdTokenData])
          indexTokenList.push(_token)
        }
      })
    setIndexTokenData(indexTokenList)

    if (!tokenOut && indexTokenList[1]) {
      setTokenOut(indexTokenList[1].id)
      dispatch({
        type: 'tokens/save',
        payload: {
          chartSelectedTokenId: indexTokenList[1].id,
        },
      })
    }
  }, [tokenPairs, tokenDatas, dispatch, tokenOut])

  useEffect(() => {
    let collsTokenList: TokenDataType[] = []
    if (tokenPairs && tokenDatas.length)
      tokenPairs.forEach((v) => {
        if (v.indexToken.toLowerCase() === tokenOut.toLowerCase()) {
          const _token = findToken(v.collateralToken, [...tokenDatas, osdTokenData])
          collsTokenList.push(_token)
        }
      })
    setCollsTokenData(collsTokenList)
    if (!collsTokenList.find((v) => v.id === tokenIn) && collsTokenList[0]) {
      setTokenIn(collsTokenList[0].id)
    }
  }, [tokenOut])








  const cost = useMemo(() => {
    return calUsdPrice(amountIn, curPair.userCollateralPrice)
  }, [amountIn, curPair])

  useEffect(() => {
    if (curPair) {
      setTargetPrice(curPair.userIndexPrice)
    }
  }, [curPair])


  useEffect(() => {
    const findPos = () => {
      console.log(curPair, amountIn, amountOut, 'findPos')
      const positon = positionList.find(
        (pair) =>
          pair.collateralToken.toLowerCase() === tokenIn.toLowerCase() &&
          pair.indexToken.toLowerCase() === tokenOut.toLowerCase() &&
          pair.isLong === isLong,
      )
      return positon || {}
    }
    const _curpair = findPair(tokenIn, tokenOut, tokenPairs)
    setCurPair(_curpair)
    dispatch({
      type: 'tokens/save',
      payload: {
        chartSelectedPair: _curpair,
      },
    })

    setCurPos(findPos())
  }, [tokenIn, tokenOut, tokenPairs && tokenPairs.length > 0, positionList.length > 0])




  const updateOpData = (amountInRaw, amountOutRaw) => {
    try {
      let indexPrice = curPair.indexPrice;
      if (tradeType === 'limit') {
        indexPrice = formatPriceToBN(String(targetPrice), 0)
      }
      console.log(curPos,
        curPair,
        amountInRaw,
        amountOutRaw,
        curPair.collateralPrice,
        indexPrice, 'opIncreasePositionParams')
      const data = opIncreasePosition(
        curPos,
        curPair,
        amountInRaw,
        amountOutRaw,
        curPair.collateralPrice,
        indexPrice,
      )

      console.log(new BN(data.tradingFee.toString()).div(Math.pow(10, curPair.userCollateralTokenDecimal)).toFixed(4), data.tradingFee, curPair.collateralTokenDecimal, 'opIncreasePosition')

      setCurData({
        ...data,
        liqPrice: formatBnPrice(data.indexLiqPrice),
        tradingFeeRate: new BN(data.tradingFeeRate.toString()).div(TRADING_FEE_RATE_PRECISION).multipliedBy(100).toFixed(4),
        hourFundingRate: new BN(data.hourFundingRate.toString()).div(FUNDING_RATE_PRECISION).multipliedBy(100).toFixed(6),
        tradingFee: new BN(data.tradingFee.toString()).div(Math.pow(10, curPair.userCollateralTokenDecimal)).toFixed(4),
        amountInRaw,
        amountOutRaw,
      })
    } catch (err) {
      setCurData({})
      console.log(err)
      message.error(err.message || 'unknown error')
    }

  }

  const calcMaginIndex = (size, margin) => {


    try {
      let indexPrice = curPair.indexPrice;
      if (tradeType === 'limit') {
        indexPrice = formatPriceToBN(String(targetPrice), 0)
      }
      console.log(curPos,
        curPair,
        BigInt(leverage * LEVERAGE_PRECISION),
        BigInt(margin),
        BigInt(size),
        curPair.collateralPrice,
        indexPrice)
      const data = calcOpIncreasePosition(
        curPos,
        curPair,
        BigInt(leverage * LEVERAGE_PRECISION),
        BigInt(margin),
        BigInt(size),
        curPair.collateralPrice,
        indexPrice,
      )

      console.log(data)

      updateOpData(data.marginDelta, data.sizeDelta)
      return data
    } catch (err) {
      console.log(err)
      return { marginDelta: -1, sizeDelta: -1 }
    }
  }

  const calAmountOut = (_amountIn: number) => {
    if (!_amountIn) {
      setAmountOut(0)
      return
    }
    const amountInRaw = amountRaw(_amountIn.toString(), curPair.userCollateralTokenDecimal)
    let { sizeDelta } = calcMaginIndex(0, amountInRaw)
    if (sizeDelta < 0) sizeDelta = 0n
    const _size = formatSat(sizeDelta.toString(), curPair.userIndexTokenDecimal)

    console.log(_size, 'setAmountOut')

    setAmountOut(_size)
  }

  const changeAmountIn = async (value: number) => {
    setAmountIn(value)
    setType('in')

    const env = await envir(currentWallet, walletChainId)
    if (!env) return
    debounce(calAmountOut, 500)(value)
  }

  const onChangeTokenIn = (token: TokenDataFullType) => {
    setTokenIn(token.id)
  }
  const calAmountIn = (_amountOut: number) => {
    if (!_amountOut) {
      setAmountIn(0)
      return
    }
    const amountOutRaw = amountRaw((_amountOut || 0).toString(), curPair.userIndexTokenDecimal)
    let { marginDelta } = calcMaginIndex(amountOutRaw, 0)
    if (marginDelta < 0) marginDelta = 0n
    const _margin = formatSat(marginDelta.toString(), curPair.userCollateralTokenDecimal)

    setAmountIn(_margin)
  }

  useEffect(() => {
    if (type === 'in') {
      calAmountOut(amountIn)
    } else if (type === 'out') {
      calAmountIn(amountOut)
    }
  }, [leverage, tokenIn, tokenOut])

  const changeAmountOut = async (value: number) => {
    setAmountOut(value)
    setType('out')
    const env = await envir(currentWallet, walletChainId)
    if (!env) return
    debounce(calAmountIn, 500)(value)
  }

  const onChangeTokenOut = (_token: TokenDataFullType) => {
    setTokenOut(_token.id)
    dispatch({
      type: 'tokens/save',
      payload: {
        chartSelectedTokenId: _token.id,
      },
    })
  }

  const handleLimitIncreaseAndDecreasePosition = async () => {
    try {
      const targetPriceRaw = formatPriceToBN(String(targetPrice), 0)
      const tpPriceRaw = formatPriceToBN(String(tpPrice), 0)
      const slPriceRaw = formatPriceToBN(String(slPrice), 0)
      const execFee = await FutureLib.getLimitMinExecFee(walletChainId)
      const { minSizeDelta, maxSizeDelta } = calcLimitDecreaseSizeDelta(
        curPair,
        isLong,
        curData.notionalDelta,
        curPair.collateralPrice,
        curPair.indexPrice,
        slPriceRaw,
        tpPriceRaw,
      )
      const params: Future.LimitInAndDeParams = {
        _collateralToken: tokenIn,
        _indexToken: tokenOut,
        _isLong: isLong,
        _increaseMarginDelta: curData.amountInRaw,
        _increaseNotionalDelta: curData.notionalDelta, // ???  
        _increaseSizeDelta: calcLimitIncreaseSizeDelta(
          curPair,
          isLong,
          curData.notionalDelta,
          // curPair.collateralPrice, 1
          formatPriceToBN('1', 0),
          curPair.indexPrice,
          targetPriceRaw
        ),
        _increaseExecFee: execFee,
        _decreaseNotionalDelta: curData.notionalDelta,
        _decreaseMinSizeDelta: minSizeDelta,
        _decreaseMaxSizeDelta: maxSizeDelta,
        _decreaseExecFee: execFee
      }
      const result = await FutureLib.limitIncreaseAndDecreasePosition(walletChainId, params, currentWallet)
      return result
    } catch (error) {
      console.log(error)

      message.error(error.message || 'unknown error');
    }

  }
  // tpsl
  const handleLimitIncreasePosition = async () => {

    try {
      const targetPriceRaw = formatPriceToBN(String(targetPrice), 0)
      const execFee = await FutureLib.getLimitMinExecFee(walletChainId)

      const params: Future.LimitInParams = {
        _collateralToken: tokenIn,
        _indexToken: tokenOut,
        _isLong: isLong,
        _marginDelta: curData.amountInRaw,
        _notionalDelta: curData.notionalDelta, // ???  
        _sizeDelta: calcLimitIncreaseSizeDelta(
          curPair,
          isLong,
          curData.notionalDelta,
          curPair.collateralPrice,
          curPair.indexPrice,
          targetPriceRaw
        ),
        _execFee: execFee,

      }
      const result = await FutureLib.limitIncreasePosition(walletChainId, params, currentWallet)
      return result
    } catch (error) {
      console.log(error)
      message.error(error.message || 'unknown error');
    }

  }

  const handleIncreasePosition = async () => {
    const reslut = await dispatch({
      type: 'future/increasePosition',
      payload: {
        params: {
          _collateralToken: tokenIn,
          _indexToken: tokenOut,
          _isLong: isLong,
          _amountIn: curData.amountInRaw,
          _notionalDelta: curData.notionalDelta,
        },
      },
    })
    return reslut
  }


  const renderLimitResult = (hash: string) => {
    Modal.destroyAll()
    const { exporerTxUrl = '' } = getChainById(walletChainId)
    Modal.warning({
      title: '',
      width: 420,
      className: styles.modal,
      wrapClassName: styles.modalWrap,
      icon: null,
      getContainer: () => document.getElementById('J_Page') || document.body,
      okText: 'Close',

      onOk: () => {
        setAmountIn(0)
        setAmountOut(0)
        dispatch({
          type: 'future/getPairs',
        })
      },
      content: (
        <div className={styles.modalContainer}>
          <div className={styles.icon}>
          <Icon type="icon-success" style={{width:60,height:60}} />

          </div>

          <p className={styles.title}>Success</p>
          <p className={styles.subTitle}>
            {isLong ? 'Long' : 'Short'} {amountOut} {tokenOutObj.symbol} with ${amountIn} {tokenInObj.symbol} collateral
          </p>

          <a href={`${exporerTxUrl}/${hash}`} target="_blank" rel="noreferrer" className={styles.link}>View on explorer</a>

        </div>
      )
    })
  }

  const handleSubmit = async () => {
   
    const env = await envir(currentWallet, walletChainId)
    if (!env) return
    setSubmiting(true)
    let res: any;





    if (tradeType === 'market') {
      // 
      res = await handleIncreasePosition()
    } else {
      // 

      if (tpsl) {
        res = await handleLimitIncreaseAndDecreasePosition()
      } else {
        res = await handleLimitIncreasePosition()
      }
    }




    if (res && res.hash) {
      console.log(res, 'res')
      if (tradeType === 'limit') {
        renderLimitResult(res.hash)
      } else {
        Result({
          msg: isLong ? (
            <div className={`${styles.result_icon} ${styles.green}`}>
              <Icon type="icon-up" />
              {_('long_opend')}
            </div>
          ) : (
            <div className={`${styles.result_icon} ${styles.red}`}>
              <Icon type="icon-down1" />
              {_('short_opend')}
            </div>
          ),
          txid: res.hash,
          token: tokenOutObj,
          chainId: walletChainId,
          onOk: () => {
            setAmountIn(0)
            setAmountOut(0)
            dispatch({
              type: 'future/getPairs',
            })
          },
        })
        dispatch({
          type: 'future/getPositionList',
        })
      }
    }

    setSubmiting(false)

  }





  const loading = !!tokenPairs && tokenPairs.length < 1 && !!tokenDatas
  const TPSLOpt = {
    slPrice,
    setSlPrice,
    tpPrice,
    setTpPrice,
    tpsl,
    setTPSL: (_tpsl) => {
      setTPSL(_tpsl);
      if (_tpsl) {
        const _maxPrice = Number((targetPrice * 1.1).toFixed(2));
        const _minPrice = Number((targetPrice * 0.9).toFixed(2));
        setTpPrice(isLong ? _maxPrice : _minPrice)
        setSlPrice(isLong ? _minPrice : _maxPrice)
      }
    },
    targetPrice,
    amountOut,
    isLong,
    tradeType,
    cost
  }



  const beforeSubmit = () => {
    if (tradeType === 'limit') {
      Modal.destroyAll()
      Modal.warning({
        title: '',
        width: 420,
        className: styles.modal,
        wrapClassName: styles.modalWrap,
        icon: null,
        getContainer: () => document.getElementById('J_Page') || document.body,
        okText: 'Sign transaction in wallet',
        onOk: () => { Modal.destroyAll(); handleSubmit() },
        maskClosable: true,
        content: (
          <div className={styles.modalContainer}>
            <p className={styles.title}>Confirm {isLong ? 'Long' : 'Short'}</p>
            <p className={styles.subTitle}>
              {isLong ? 'Long' : 'Short'} {amountOut} {tokenOutObj.symbol} with ${amountIn} {tokenInObj.symbol} collateral
            </p>
            <div className={styles.content} >
              <Description label='Max slippage' value="0.2%" />
              <Description label='Leverage' value={`${leverage}x`} />
              <Description label='Entry Price' value={`$${targetPrice}`} />
            </div>
          </div>
        )
      })
    } else {
      handleSubmit()
    }
  }



  return (
    <>
      <div className={styles.container}>
        <Spin spinning={loading || submiting}>
          <div className={styles.tradeTypes}>
            <span className={tradeType === 'market' ? styles.active : ''} onClick={() => { setTradeType('market') }}>
              Market
            </span>
            <span className={tradeType === 'limit' ? styles.active : ''} onClick={() => { setTradeType('limit') }}>
              Limit
            </span>
          </div>

          <div className={styles.card}>
            <div className={styles.label}>
              <div>
                {_('pay')}({_('margin')}+{_('fee')}): $
                {cost}
              </div>
              <div>
                {_('balance')}:&nbsp;
                <span
                  className="primary"
                  onClick={() => changeAmountIn(parseFloat(tokenInObj.userTokenBalance))}
                >
                  {tokenInObj.userTokenBalance}
                </span>
              </div>
            </div>
            <div className={styles.value}>
              <InputNumber
                className="inputTxt"
                value={amountIn}
                onChange={changeAmountIn}
                min={0}
                placeholder="0.00"
              />
              <ChooseToken
                currentToken={tokenInObj}
                changeToken={onChangeTokenIn}
                data={collsTokenData}
                isLogin={isLogin}
                loading={loading}
              />
            </div>
          </div>

          <div className={styles.card}>
            <div className={styles.label}>
              <div className={isLong ? styles.green : styles.red}>
                {isLong ? _('long') : _('short')}: ${calUsdPrice(amountOut, tradeType === 'market' ? curPair.userIndexPrice : targetPrice)}
              </div>
              {tradeType === 'market' && <div>
                {_('market_price')}: ${curPair.userIndexPrice}
              </div>}
            </div>
            <div className={styles.value}>
              <InputNumber
                className="inputTxt"
                value={amountOut}
                onChange={changeAmountOut}
                min={0}
                placeholder="0.00"
              />
              <ChooseToken
                currentToken={tokenOutObj}
                changeToken={onChangeTokenOut}
                data={indexTokenData}
                isLogin={isLogin}
                loading={loading}
              />
            </div>
          </div>
          {
            tradeType === 'limit' && <div className={styles.card}>
              <div className={styles.label}>
                <div >
                  {_('price')}
                </div>
                <div>
                  {_('market_price')}: ${curPair.userIndexPrice}
                </div>
              </div>
              <div className={styles.value}>
                <InputNumber
                  className="inputTxt"
                  value={targetPrice}
                  onChange={setTargetPrice}
                  min={isLong ? 0 : curPair.userIndexPrice}
                  max={isLong ? curPair.userIndexPrice : Infinity}
                />
                <p style={{ marginBottom: 0 }}>USD</p>
              </div>
            </div>
          }



          <TPSL
            maxLeverage={curPair.userMaxLeverage}
            leverage={leverage}
            setLeverage={setLeverage}
            {...TPSLOpt}
          />


          <div className={styles.info}>
            <div className={styles.item}>
              <div className={styles.label}>{_('entry_price')}</div>
              <div className={styles.fee}>${tradeType === 'market' ? curPair.userIndexPrice : targetPrice}</div>
            </div>
            {tradeType === 'market' && <div className={styles.item}>
              <div className={styles.label}>{_('liquidation_price')}</div>
              <div className={styles.fee}>{curData.liqPrice && `$${curData.liqPrice}`}</div>
            </div>}
            {tradeType === 'market' && <div className={styles.item}>
              <div className={styles.label}>{_('funding_rate')}</div>
              <div className={styles.fee}>{curData.hourFundingRate}%/1h</div>
            </div>}
            <div className={styles.item}>
              <div className={styles.label}>Fees</div>
              {curData.tradingFeeRate && <div className={styles.fee}>{curData.tradingFeeRate}% ({curData.tradingFee}{tokenInObj.symbol})</div>}
            </div>

          </div>

          <Btn
            height={50}
            align="center"
            onClick={beforeSubmit}
            className={isLong ? styles.green_btn : styles.red_btn}
            conditions={[
              {
                handle: !tokenIn || !tokenOut,
                txt: _('select_asset'),
              },
              {
                handle: amountIn <= 0 || amountOut <= 0,
                txt: _('input_amount'),
              },
              {
                handle: amountIn > parseFloat(tokenInObj.userTokenBalance || '0'),
                txt: _('lack_balance'),
              },
              {
                handle: tradeType === 'limit' && ((isLong && targetPrice >= curPair.userIndexPrice) || (!isLong && targetPrice <= curPair.userIndexPrice)),
                txt: _('invalid_limit_price'),
              },
              //   {
              //     handle: total > 0 && parseFloat(amountOut) > total,
              //     txt: _('not_enough', tokenOutObj.symbol),
              //   },
            ]}
          >
            {isLong ? _('open_long') : _('open_short')}
          </Btn>
        </Spin>
      </div>
    </>
  )
}

const mapStateToProps = ({
  tokens,
  wallet,
  future,
}: {
  tokens: TokenStateType
  wallet: WalletStateType
  future: FutureStateType
}) => {
  return {
    ...tokens,
    ...wallet,
    ...future,
  }
}

export default connect(mapStateToProps)(Perp)
