import Layout from '@/layout'
import { useState } from 'react'
import { connect } from 'umi'
import Chart from './chart'
import styles from './index.less'
import Panel from './panel'
import Positions from './positions'
import _ from '@/i18n'
import TradeHistory from './tradeHistory'
import LimitOrder from './LimitOrder'
import type { CommonStateType, WalletStateType } from '@/models/dataType'
import { getNetwork } from '@/common/utils'

const menuDatas = [_('positions'), _('trade_history'), _('market_trades'),_('open_orders'),]

function Trade(props: CommonStateType & WalletStateType) {
  const { isShowChart, dataSourceChainId, currentWallet = {} } = props
  const [menuIndex, setMenuIndex] = useState(0)
  const network = getNetwork(dataSourceChainId, currentWallet.network)
  const isZk = network === 'zk' || network === 'arbitrum'
  return (
    <Layout>
      <div className={styles.container}>
        <Panel network={network} />
        <div className={styles.datas}>
          {isShowChart && <Chart />}
          {isZk && (
            <div className={styles.table_datas}>
              <div className={styles.title_wrap}>
                {menuDatas.map((menu, index) => (
                  <div
                    className={index === menuIndex ? styles.current_title : styles.title}
                    key={menu}
                    onClick={() => {
                      setMenuIndex(index)
                    }}
                  >
                    {menu}
                  </div>
                ))}
              </div>
              {menuIndex === 0 && <Positions />}
              {menuIndex === 1 && <TradeHistory type="personal" />}
              {menuIndex === 2 && <TradeHistory type="market" />}
              {menuIndex === 3 &&  <LimitOrder/>}
             
            </div>
          )}
        </div>
      </div>
    </Layout>
  )
}

const mapStateToProps = ({
  common,
  wallet,
}: {
  common: CommonStateType
  wallet: WalletStateType
}) => {
  return { ...common, ...wallet }
}

export default connect(mapStateToProps)(Trade)
