import { Modal } from 'antd'
import styles from './simple.less'
import _ from '@/i18n'
import Clipboard from '@/components/clipboard'
import CopySvg from '@/assets/svg/copy.svg'
import { strAbbreviation } from '@/common/utils'
import DefultIcon from '../icon/defult'
import { getChainById } from '@/layout/header/chainData'

export function Result({
  msg,
  txid,
  token,
  onOk,
  chainId,
}: {
  msg: any
  txid: string
  chainId: string
  token?: { icon: string; symbol: string; tokenId: string }
  onOk?: any
}) {
  const { icon, tokenId, symbol } = token || {}
  const chainData = getChainById(chainId)
  Modal.warning({
    width: 420,
    title: '',
    content: (
      <div className={styles.container}>
        <div className={styles.result_title}>
          {token && (
            <div className={styles.token}>
              {icon ? <img src={icon} width={25} /> : <DefultIcon tokenId={tokenId} size={25} />}
              <div className={styles.name}>{symbol}</div>
            </div>
          )}
          {msg}
        </div>
        <div className={styles.result_link}>
          <a
            className="primary"
            href={`${chainData.exporerTxUrl}/${txid}?network=${chainData.network}`}
            target="_blank"
            rel="noreferrer"
          >
            {_('browse')}: {strAbbreviation(txid, [16, 16])}
          </a>
          <Clipboard text={txid}>
            <img src={CopySvg} style={{ cursor: 'pointer' }} />
          </Clipboard>
        </div>
      </div>
    ),
    onOk,
    okText: _('ok'),
    wrapClassName: styles.modal,
    getContainer: () => document.getElementById('J_Page') || document.body,
  })
}
