import { useEffect, useState, useMemo } from 'react'
import { Switch, Slider, InputNumber, Checkbox } from 'antd'
import _ from '@/i18n'
import styles from './index.less'
import type { CheckboxChangeEvent } from 'antd/es/checkbox'
type propsType = {
  maxLeverage: number
  leverage: number
  setLeverage: any,
  tpsl: boolean,
  setTPSL: (checked: boolean) => void,
  tpPrice: number,
  setTpPrice: (value: number) => void,
  slPrice: number,
  setSlPrice: (value: number) => void,
  targetPrice: number,
  amountOut: number, //
  isLong: boolean,
  tradeType: 'market' | 'limit',
  cost: number
}
function TPSL(props: propsType) {
  const { maxLeverage, leverage, setLeverage, setTPSL, tpsl, tpPrice, setTpPrice, slPrice, setSlPrice, targetPrice, isLong, amountOut, tradeType,cost } = props
  // const [detailVisible, setDetailVisible] = useState(false)
  const [silderVisible, setSilderVisible] = useState(false)
  const [dig, setDig] = useState(leverage)
  const tpInfo = useMemo(() => {
    let amount = 0;
    let rate = 0;
    if (amountOut !== 0) {
      amount = isLong ? (tpPrice - targetPrice) * amountOut  : (targetPrice - tpPrice) * amountOut ; // 
      rate = amount/cost*100
    }
    return {
      rate: Number(rate.toFixed(2)) + '%',
      priceRate: Number(((tpPrice - targetPrice) / targetPrice * 100).toFixed(2)) + '%',
      amount: Number(amount.toFixed(2))
    }
  }, [tpPrice, targetPrice, amountOut, isLong,cost])
  const slInfo = useMemo(() =>{
    let amount = 0;
    let rate = 0;
    if (amountOut !== 0) {
      amount = isLong ? (targetPrice - slPrice) * amountOut  : (slPrice - targetPrice) * amountOut ; // 
      // rate = isLong ? (targetPrice - slPrice) / targetPrice * 100  :(slPrice - targetPrice) / targetPrice * 100 
      rate = amount/cost*100
    }
    return {
      rate: Number(rate.toFixed(2)) + '%',
      priceRate: Number(((targetPrice - slPrice) / targetPrice * 100).toFixed(2)) + '%',
      amount: Number(amount.toFixed(2))
    }
  } , [slPrice, targetPrice, amountOut, isLong,cost])

  const tiggerValue=useMemo(()=>{
    if(!amountOut) return undefined
    if(isLong){
      return Number((targetPrice-cost/amountOut)).toFixed(2)
    }else{
      return Number((Number(targetPrice)+cost/amountOut)).toFixed(2)
    }
  },[targetPrice,cost,amountOut,isLong])

  



  const onChangeDetailVisible = (e: CheckboxChangeEvent) => {
    setTPSL(e.target.checked)
  }

  const onChangeSliderVisible = (checked: boolean) => {
    setSilderVisible(checked)
  }

  const onChangeDig = (value: number) => {
    console.log(value)
    setDig(value)
    setLeverage(value)
  }

  useEffect(() => {
    if (maxLeverage < leverage) {
      onChangeDig(maxLeverage)
    }
  }, [maxLeverage])

  return (
    <>
      <div className={styles.row}>
        {tradeType === 'limit' ? <div className={styles.tpsl}>
          TP/SL <Checkbox onChange={onChangeDetailVisible} checked={tpsl} className={styles.checkbox} />
        </div> : <div className={styles.tpsl} />}
        <div className={styles.silder_trigger}>
          <div>
            <InputNumber
              // formatter={(value) => value + 'x'}
              value={dig}
              onChange={onChangeDig}
              min={1}
              max={maxLeverage || 1}
              className={styles.dig}
            />
          </div>
          <span className={styles.txt}>{_('slider')}</span>
          <Switch onChange={onChangeSliderVisible} />
        </div>
      </div>
      {silderVisible && (
        <Slider
          value={dig}
          onChange={onChangeDig}
          className={styles.slider}
          min={1}
          max={maxLeverage || 1}
        />
      )}
      {
        tradeType === 'limit' && tpsl && <>
          <div className={styles.card}>
            <div className={styles.label}>
              <div>
                {_('tp_price')}({tpInfo.priceRate} {_('up')})
              </div>
              <div>{_('estimated_profit')}</div>
            </div>
            <div className={styles.value}>
              <InputNumber
                className="inputTxt"
                value={tpPrice}
                onChange={setTpPrice}
                min={isLong?targetPrice:0}
                max={isLong?Infinity:targetPrice}
                formatter={value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
              />
              <div className={styles.amount_green}>${tpInfo.amount}({tpInfo.rate})</div>
            </div>
          </div>
          <div className={styles.card}>
            <div className={styles.label}>
              <div>
                {_('tigger_price')}({slInfo.priceRate} {_('down')})
              </div>
              <div>{_('estimated_loss')}</div>
            </div>
            <div className={styles.value}>
              <InputNumber
                className="inputTxt"
                value={slPrice}
                onChange={setSlPrice}
                min={isLong?tiggerValue:targetPrice}
                max={isLong?targetPrice:tiggerValue}
                formatter={value => `${isLong?'':''} $ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
              />
              <div className={styles.amount_red}>${slInfo.amount}({slInfo.rate})</div>
            </div>
          </div>
        </>
      }


    </>
  )
}

export default TPSL
