import { Modal } from 'antd'
import styles from './index.less'
import PanelMenu from '@/components/menu'
import { ClosePositionDatas } from '@/common/config'
import ClosePosition from './closePosition'
import { useState } from 'react'

export default function ClosePositionMenu(props) {
  const { visible, setVisible, currentToken } = props
  const [menutIndex, setMenuIndex] = useState(1)

  return (
    <Modal
      width={420}
      footer={null}
      open={visible}
      getContainer={() => document.getElementById('J_Page') || document.body}
      onCancel={() => setVisible(false)}
      closable={false}
      wrapClassName={styles.modal}
    >
      <PanelMenu
        menuDatas={ClosePositionDatas}
        menuIndex={menutIndex}
        setMenuIndex={setMenuIndex}
      />
      {/* <div style={{ display: menutIndex === 0 ? 'block' : 'none' }}>
        <AddMargin currentToken={currentToken} closeModal={() => setVisible(false)} />
      </div> */}
      <div style={{ display: menutIndex === 1 ? 'block' : 'none' }}>
        <ClosePosition currentToken={currentToken} closeModal={() => setVisible(false)} />
      </div>
    </Modal>
  )
}
