import _ from '@/i18n'
import styles from '../margin/info.less'
import BN from 'bignumber.js'
import type { FutureStateType } from '@/models/dataType'
import { connect } from 'umi'
import { opDecreasePosition } from '@1kx/typechain/helper/future'
import { useEffect, useState } from 'react'
import { formatBnPrice, formatLeverage, formatMr, formatPnl } from '../perp/utils'
import { amountRaw, formatSat } from '@/common/utils'
import { ArrowRight } from '@/components/ui'
type dataType = {
  isPositive: boolean
  pnlStr: string
  roeStr: string
  leverage: string
  receive: number
  mr: string
  liqPrice: string
  leftMargin: string
}
type propsType = {
  currentToken: any
  amountIn: string
}
function Info(props: propsType) {
  const { currentToken, amountIn } = props
  const {
    collateralTokenObj,
    userSize: size,
    margin,
    userMargin,
    openNotional,
    pair,
  } = currentToken
  const [data, setData] = useState<dataType>({} as dataType)

  useEffect(() => {
    const amountInRaw = amountRaw(amountIn || '0', pair.userIndexTokenDecimal)
    // console.log({
    //   pos: currentToken,
    //   pair: pair,
    //   size: BigInt(amountInRaw),
    //   collateralPrice: pair.collateralPrice,
    //   indexPrice: pair.indexPrice,
    // })
    const closeShortRes = opDecreasePosition(
      currentToken,
      pair,
      BigInt(amountInRaw),
      pair.collateralPrice,
      pair.indexPrice,
    )
    // console.log('closeShortRes', closeShortRes)
    const { isPositive, pnlStr, roeStr } = formatPnl(
      closeShortRes.pnl,
      pair.userCollateralTokenDecimal,
      margin,
    )
    const leverage = formatLeverage(margin, openNotional)
    const receive = formatSat(
      closeShortRes.receiveMargin.toString(),
      pair.userCollateralTokenDecimal,
    )
    let mr = formatMr(closeShortRes.mr)
    let liqPrice = formatBnPrice(closeShortRes.indexLiqPrice)

    const leftMargin = new BN(size - amountIn).div(size).multipliedBy(userMargin).toFixed(6)

    if (amountIn >= size) {
      mr = '--'
      liqPrice = '--'
    }
    setData({
      isPositive,
      pnlStr,
      roeStr,
      leverage,
      receive,
      mr,
      liqPrice,
      leftMargin,
    })
  }, [currentToken, amountIn])

  return (
    <div className={styles.info}>
      <div className={styles.item}>
        <div className={styles.label}>
          {_('pnl')}/{_('roe')}
        </div>
        <div className={data.isPositive ? 'primary' : 'red'}>
          {data.pnlStr}/{data.roeStr}
        </div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>{_('leverage')}</div>
        <div className={styles.value}>{data.leverage}</div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>{_('margin')}</div>
        <div className={styles.value}>
          {userMargin} <ArrowRight /> {data.leftMargin}
        </div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>{_('margin_rate')}</div>
        <div className={styles.value}>{data.mr}</div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>EST {_('liquidation_price')}</div>
        <div className={styles.value}>${data.liqPrice}</div>
      </div>
      <div className={styles.item}>
        <div className={styles.label}>{_('receive')}</div>
        <div className={data.isPositive ? 'primary' : 'red'}>
          {data.receive} {_('in')} {collateralTokenObj.symbol}
        </div>
      </div>
    </div>
  )
}

const mapStateToProps = ({ future }: { future: FutureStateType }) => {
  return {
    ...future,
  }
}

export default connect(mapStateToProps)(Info)
