import { useState } from 'react'
import { TradeMenuDatas } from '@/common/config'
import PanelMenu from '@/components/menu'
import Swap from '../swap'
import Perp from '../perp'
// import Short from '../short'
import styles from './index.less'
import { useEffect } from 'react'
// import arrowSvg from '@/assets/svg/arrow.svg'

export default function Panel(props: { network: string }) {
  const { network } = props
  console.log(network,'networknetworknetworknetwork')
  const [menuIndex, setMenuIndex] = useState(0)
  let panels = [
    <Swap key="swap" />,
    <Perp key="long" isLong={true} />,
    <Perp key="short" isLong={false} />,
  ]
  useEffect(()=>{
    if(network !== 'zk'&&network !== 'sepolia'&&network !== 'arbitrum'){
      setMenuIndex(0)
    }
  },[network])

  if (network === 'zk'||network === 'sepolia'||network === 'arbitrum') {
    console.log(network,'networknetworknetworknetwork1')
    panels.length = 3
    TradeMenuDatas[1].disabled = false
    TradeMenuDatas[2].disabled = false
  } else {
    panels.length = 1
    TradeMenuDatas[1].disabled = true
    TradeMenuDatas[2].disabled = true
  }

  return (
    <div className={styles.container}>
      <PanelMenu
        menuIndex={menuIndex}
        setMenuIndex={setMenuIndex}
        menuDatas={TradeMenuDatas}
        className={styles.menu}
      />
      {panels.map((panel, index) => {
        return (
          // eslint-disable-next-line react/no-array-index-key
          <div key={index} style={{ display: index === menuIndex ? 'block' : 'none' }}>
            {panel}
          </div>
        )
      })}
    </div>
  )
}
