import styles from './index.less'
import { Table } from 'antd'
import _ from '@/i18n'
import { useEffect, useState } from 'react'
import type { FutureStateType, HistoryStateType, WalletStateType } from '@/models/dataType'
import { connect, useDispatch } from 'umi'
import { formatDate, strAbbreviation } from '@/common/utils'
import { Loading3QuartersOutlined } from '@ant-design/icons'
import { getChainById } from '@/layout/header/chainData'
import { getNetwork } from '@/common/utils'

const pageCount = 10

function TradeHistory(props) {
  const { account, type, positionList, walletChainId,dataSourceChainId,currentWallet={} } = props
  console.log(props,'TradeHistory')
  const dispatch = useDispatch()
  const [totalCount, setTotalCount] = useState(0)
  const [currentPage, setCurrentPage] = useState(1)
  const [records, setRecords] = useState([])
  const [loading, setLoading] = useState(false)
  const [varA, setVarA] = useState(0)
  const chainData = getChainById(walletChainId)
  const network = getNetwork(dataSourceChainId, currentWallet.network)

  const getCount = async () => {
    if (type === 'personal' && !account) {
      return setTotalCount(0)
    }
    const param = type === 'personal' ? { account,network } : {network}

    const count = await dispatch({
      type: 'history/getOrderAggregationCount',
      payload: param,
    })
    setTotalCount(count)
  }

  const getRecords = async (page: number = 1) => {
    if (type === 'personal' && !account) {
      return setRecords([])
    }
    let param = {
      skip: (page - 1) * pageCount,
      first: pageCount,
      network
    }
    if (type === 'personal') {
      param.account = account
    }
    const recordsRes = await dispatch({
      type: 'history/getOrderAggregation',
      payload: param,
    })
    setRecords(recordsRes)
  }

  useEffect(() => {
    const fun = async () => {
      setLoading(true)
      await getCount()
      await getRecords()
      setLoading(false)
    }
    fun()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, positionList.length])

  useEffect(() => {
    const timeoutA = setTimeout(async () => {
      setVarA(varA + 1)
      await getCount()
      getRecords(currentPage)
    }, 30 * 1e3)

    return () => {
      clearTimeout(timeoutA)
    }
  }, [varA])

  const onChange = async (value: number) => {
    console.log(value)
    setCurrentPage(value)
    getRecords(value)
  }

  const columns = [
    {
      title: '',
      dataIndex: 'timestamp',
      key: 'timestamp',
      width: 180,
      render: (text) => formatDate(text * 1000, 'YYYY/MM/DD, HH:mm A'),
    },
    {
      title: '',
      dataIndex: 'content',
      key: 'content',
    },
    {
      title: '',
      dataIndex: 'account',
      key: 'account',
      width: 162,
      render: (text) => {
        return (
          <>
            {_('owner')}:&nbsp;
            <a
              className="primary"
              href={`${chainData.exporerAccountUrl}/${text}?network=${chainData.network}`}
              target="_blank"
              rel="noreferrer"
            >
              {strAbbreviation(text)}
            </a>
          </>
        )
      },
    },
  ]

  return (
    <div className={styles.container}>
      <div className={styles.table_wrap}>
        <Table
          columns={columns}
          dataSource={records}
          className={styles.table}
          rowKey="timestamp"
          loading={{
            spinning: loading && records.length < 1,
            indicator: <Loading3QuartersOutlined spin style={{ fontSize: 30 }} />,
          }}
          pagination={{
            onChange,
            total: totalCount,
            showTotal: (total) => {
              const lastPageCount =
                pageCount * currentPage > total ? total : pageCount * currentPage
              return `${pageCount * (currentPage - 1) + 1} - ${lastPageCount} of ${total}`
            },
          }}
        />
      </div>
    </div>
  )
}

const mapStateToProps = ({
  future,
  history,
  wallet,
}: {
  future: FutureStateType
  history: HistoryStateType
  wallet: WalletStateType
}) => {
  return {
    ...future,
    ...history,
    ...wallet,
  }
}

export default connect(mapStateToProps)(TradeHistory)
