import styles from './index.less'
import { Table } from 'antd'
import _ from '@/i18n'
import { useEffect, useState,useRef } from 'react'
import Margin from '../margin'
import type { ColumnsType } from 'antd/lib/table'
import type {
  FutureStateType,
  TokenDataFullType,
  TokenStateType,
  WalletStateType,
} from '@/models/dataType'
import { connect, useDispatch } from 'umi'
import { calUsdPrice, formatSat, toFixed } from '@/common/utils'
import { Loading3QuartersOutlined } from '@ant-design/icons'
import CloseOrder from '../closePosition'
import { calcPnl, calcMr } from '@1kx/typechain/helper/future'
import { formatLeverage, formatMr, formatPnl } from '../perp/utils'

function Positions(props) {
  const { positionList, account, tokenPairs, tokenDatas } = props
  const dispatch = useDispatch()
  const [marginVisible, setMarginVisible] = useState(false)
  const [closeOrderVisible, setCloseOrderVisible] = useState(false)
  const [currentToken, setCurrentToken] = useState({})
  const [loading, setLoading] = useState(false)
  const [varA, setVarA] = useState(0)
  const intervalRef = useRef<any>(null);

  

  useEffect(() => {
    const fun = async () => {
      if (account) {
        setLoading(true)
        await dispatch({
          type: 'future/getPositionList',
        })
        setLoading(false)
      }
    }
    if(tokenPairs && tokenPairs.length > 0){
      fun()
    }
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, tokenPairs])

  useEffect(() => {
    intervalRef.current = setInterval(async () => {
     
      if (account && tokenPairs && tokenPairs.length > 0) {
        // dispatch({
        //   type: 'future/getPositionList',
        // })
      }
    }, 5 * 1e3)

    return () => {
      clearInterval(intervalRef.current);
    }
  }, [account,tokenPairs,dispatch])

  const showMarginModal = (token) => {
    setCurrentToken(token)
    setMarginVisible(true)
  }
  const closeOrder = (token) => {
    setCurrentToken(token)
    setCloseOrderVisible(true)
  }
  const columns: ColumnsType<TokenDataFullType> = [
    {
      title: (
        <div>
          {_('positions')}
          <br />
          {_('funding_rate')}
        </div>
      ),
      dataIndex: 'positions',
      key: 'positions',
      align: 'left',
      render: (text, record) => {
        const { indexTokenObj, isLong, pair, openNotional, margin } = record
        const fundingRate = (isLong ? pair.longFundingRate : pair.shortFundingRate).toString()
        const userFundingRate = formatSat(fundingRate, 7)

        // const leverage = margin
        //   ? new BN(openNotional.toString()).div(margin.toString()).toFixed(2)
        //   : 0

        const leverage = formatLeverage(margin, openNotional)
        return (
          <div className={styles.type}>
            <span className={isLong ? styles.green : styles.red}>{isLong ? 'L' : 'S'}</span>{' '}
            {indexTokenObj.symbol} <span className={styles.text}>{leverage}</span>
            <br />
            <span className={styles.text}>{userFundingRate}%/1h</span>
          </div>
        )
      },
    },
    {
      title: (
        <div>
          {_('size')}
          <br /> {_('pnl')}/{_('roe')}
        </div>
      ),
      dataIndex: 'userSize',
      key: 'userSize',
      align: 'right',
      render: (text, record) => {
        const { indexTokenObj, pair, margin } = record
        let pnl = calcPnl(record, pair, pair.collateralPrice, pair.indexPrice)
        const { isPositive, pnlStr, roeStr } = formatPnl(
          pnl,
          pair.userCollateralTokenDecimal,
          margin,
        )

        return (
          <>
            <div>
              {toFixed(text, 4)} {indexTokenObj.symbol}
            </div>

            <div className={isPositive ? styles.green : styles.red}>
              {pnlStr} ({roeStr})
            </div>
          </>
        )
      },
    },

    {
      title: (
        <>
          {_('margin')}
          <br />
          {_('value')}
        </>
      ),
      dataIndex: 'userMargin',
      key: 'userMargin',
      align: 'right',
      render: (text, record) => {
        const { collateralTokenObj, pair } = record
        return (
          <div>
            {text} {collateralTokenObj.symbol} <br />${calUsdPrice(text, pair.userCollateralPrice)}
          </div>
        )
      },
    },

    {
      title: (
        <>
          {_('entry_price')}
          <br />
          {_('liq_price')}
        </>
      ),
      dataIndex: 'userEntryIndexPrice',
      key: 'userEntryIndexPrice',
      align: 'right',
      render: (text, record) => {
        const { userLiqPrice } = record
        return (
          <div>
            ${text}
            <br />${userLiqPrice}
          </div>
        )
      },
    },

    {
      title: (
        <>
          {_('mr')}
          <br />
          {_('mmr')}
        </>
      ),
      dataIndex: 'mr',
      key: 'mr',
      align: 'right',
      render: (text, record) => {
        const { userMmr, pair } = record

        const mr = calcMr(record, pair, pair.collateralPrice, pair.indexPrice)
        const mr_str = formatMr(mr)
        return (
          <div>
            {mr_str}
            <br />
            {userMmr}%
          </div>
        )
      },
    },

    {
      title: _('manage'),
      dataIndex: 'manage',
      key: 'manage',
      align: 'center',
      render: (text, record) => {
        return (
          <div className={styles.action}>
            <span onClick={() => showMarginModal(record)}>{_('edit_margin')}</span>
            <span onClick={() => closeOrder(record)} className={styles.close}>
              {_('close')}
            </span>
          </div>
        )
      },
    },
  ]
  return (
    <>
      <Table
        columns={columns}
        dataSource={positionList.filter((v) => v.size > 0&&v.userSize)}
        className={styles.table}
        rowKey="id"
        loading={{
          spinning: loading && positionList.length < 1 && !!tokenDatas,
          indicator: <Loading3QuartersOutlined spin style={{ fontSize: 30 }} />,
        }}
      />
      {marginVisible && (
        <Margin visible={marginVisible} setVisible={setMarginVisible} currentToken={currentToken} />
      )}
      {closeOrderVisible && (
        <CloseOrder
          visible={closeOrderVisible}
          setVisible={setCloseOrderVisible}
          currentToken={currentToken}
        />
      )}
    </>
  )
}

const mapStateToProps = ({
  tokens,
  future,
  wallet,
  loading,
}: {
  tokens: TokenStateType
  future: FutureStateType
  wallet: WalletStateType
  loading: any
}) => {
  const effects = loading.effects
  return {
    ...tokens,
    ...future,
    ...wallet,
    loading: effects['future/getPositionList'] || false,
  }
}

export default connect(mapStateToProps)(Positions)
